import axios from 'axios';
import Config from '../config.js';

let base =Config.base;

export const requestLogin = params => { return axios.post(`${base}/login`, params).then(res => {console.log(res);return res.data}); };

export const changePwd = params => {return axios.post(`${base}/changePwd`,params).then(res => res.data);}

export const getUserList = params => { return axios.get(`${base}/user/list`, { params: params }); };

export const getUserListPage = params => { return axios.get(`${base}/user/listpage`, { params: params }); };

export const removeUser = params => { return axios.get(`${base}/user/remove`, { params: params }); };

export const batchRemoveUser = params => { return axios.get(`${base}/user/batchremove`, { params: params }); };

export const editUser = params => { return axios.get(`${base}/user/edit`, { params: params }); };

export const addUser = params => { return axios.get(`${base}/user/add`, { params: params }); };

export const getBmPerson = params => {return axios.post(`${base}/bm/getbm`,{params:params}).then(res => res)};
export const createNewBm = params => {return axios.post(`${base}/bm/createBm`,{params:params}).then(res => res.data)};
export const editBm = params => {return axios.post(`${base}/bm/editBm`,{params:params}).then(res => res.data)};
export const deleteBm = params => {return axios.post(`${base}/bm/deleteBm`,{params:params}).then(res => res.data)};

export const findArea = params => {return axios.post(`${base}/bm/findArea`,{params:params}).then(res => res.data)};
export const createArea = params => {return axios.post(`${base}/bm/createArea`,{params:params}).then(res => res.data)};



