<template>
	<section>
		<el-col :span="24" class="toolbar" style="padding-bottom: 0px;">
		<el-form :inline="true" :model="filter">
			<el-form-item prop='name' label='生产厂家'>
				<el-input v-model="filter.name" placeholder=""></el-input>
			</el-form-item>
			 <el-form-item label="状态" prop='status'>
			    <el-select v-model="filter.status">
			      <el-option label="所有" value="0"></el-option>
			      <el-option label="在用" value="1"></el-option>
			      <el-option label="停用" value="2"></el-option>
			    </el-select>
			  </el-form-item>
			<el-form-item>
				<el-button type="primary" v-on:click="getBms">查询</el-button>
				<el-button type="primary" @click="handleAdd">新增</el-button>
			</el-form-item>
		</el-form>
	</el-col>
	<my-el-table 
		ref="tableclick" 
		@handleDelete='handleDelete'
		@handleUp='handleUp'
		@handleDown='handleDown'
		:table="dataTable"  >
	<template slot-scope="props" slot="name">
        <a class="template-username" :rguid='props.obj.row.id' href='javascript:void(0);' @click='EditEach(props.obj.$index,props.obj.row)'>{{ props.obj.row.name }}</a>
	</template>
	<template slot-scope="props" slot="name">
        <a class="template-username" :rguid='props.obj.row.id' href='javascript:void(0);' @click='EditEach(props.obj.$index,props.obj.row)'>{{ props.obj.row.name }}</a>
	</template>
	<template slot-scope="props" slot="stop_flag">
        <span :rguid='props.obj.row.id' href='javascript:void(0);'>{{ props.obj.row.stop_flag==0?'启用中':'停用中'}}</span>
	</template>
	</my-el-table>
	<my-new-bm :showData="editData" @OnSure='editSure' v-loading='diaLoading'></my-new-bm>
	</section>
</template>

<script>
	import { getUserListPage ,getBmPerson,createNewBm,editBm,deleteBm} from '../../api/api'
	
	export default{
		data(){
			return{
				diaLoading:false,
				filter:{
					name:'',
					status:'所有',
					type:'0',
				},
				sels:[],
				dataTable:{
					loading:false,
					hasSelect:true,
					notHasIndex:false,
					hasOperation:true,
					operation: {             
		              label: '操作',                
			              width: '200',               
			              className: '',               
			              data: [
			              	{
			                  label: '上移',                
			                  Fun: 'handleUp',         
			                  id: '3'                   
			               },
			               {
			                  label: '下移',                
			                  Fun: 'handleDown',         
			                  id: '4'                   
			               },
			                {
			                  label: '删除',                
			                  Fun: 'handleDelete',         
			                  id: '2'                   
			                }
		              ]
		            },
		            tr:[
					{label:'生产厂家',prop:'name',width:120,show:'template'
				},
					{label:'备注',prop:'bz','minWidth':180},
					{label:'创建人',prop:'create_man',width:120},
					{label:'创建时间',prop:'create_date',width:120},
					{label:'停用状态',prop:'stop_flag',width:120,show:'template'},
					{label:'停用人',prop:'stop_man',width:120},
					{label:'停用时间',prop:'stop_date',width:120},
					],
					data:[]
				},
				editData:{
					title:'新增生产厂家',
					label:'生产厂家',
					type:0,
					dialogVisible:false,
					resize:'none',
					form:{
						name:'',
						bz:'',
						flag:0,
						tysm:''
					},
					rules:{
						name:[
							{ required: true, message: '编码名称不能为空', trigger: 'blur' }
						]
					}
				}
			}
		},
		methods:{
			getBms() {
				let para = {
					page: 1,
					name:this.filter.name,
					status:this.filter.status=='所有'?'0':this.filter.status,
					type:this.filter.type
				};
				this.dataTable.loading = true;
				getBmPerson(para).then((res) => {
					this.dataTable.data = res.data.bms;
					this.dataTable.loading = false;
				});
//				getUserListPage(para).then((res) => {
//					this.dataTable.data = res.data.users;
//					this.dataTable.loading = false;
//				});
			},
			clearEditForm(){
				this.editData.form={
					name:'',
					bz:'',
					flag:0,
					tysm:''
				};
			},
			handleDelete(index,row){
				var $this=this;
				$this.$confirm('确定删除该条编码么？','操作提示',{type:'warning'}).then((a) => {
					deleteBm().then(data => {
						if(!data.success){
							$this.$message.error(data.msg);
							return false;
						}
						$this.$message({
				          message: '删除成功',
				          type: 'success'
				        });
						$this.getBms();
					});
				});
			},
			handleUp(index,row){
				this.dataTable.loading=true;
				editBm({upflag:0}).then(data => {
					this.dataTable.loading=false;
					if(!data.success){
							this.$message.error(data.msg);
							return false;
						}
					this.$message({
			          message: '操作成功',
			          type: 'success'
			        });
					this.getBms();
				});
			},
			handleDown(index,row){
				this.dataTable.loading=true;
				editBm({upflag:1}).then(data => {
					this.dataTable.loading=false;
					if(!data.success){
							this.$message.error(data.msg);
							return false;
						}
					this.$message({
			          message: '操作成功',
			          type: 'success'
			        });
					this.getBms();
				});
			},
			handleAdd(){
				this.editData.type=0;
				this.clearEditForm();
				this.editData.dialogVisible=true;
			},
			editSure(valid){
				var $this=this;
				if(!valid)return false;
				$this.diaLoading=true;
				if($this.editData.type==0){
					let para={
						name:this.editData.form.name,
						bz:this.editData.form.bz
					}
					createNewBm(para).then(data => {
						$this.diaLoading=false;
						if(!data.success){
							$this.$message.error(data.msg);
							return false;
						}
						$this.$message({
						message:'新增成功',
						type:'success'
					});
					$this.editData.dialogVisible=false;
					$this.getBms();
					});
					return false;
				}
				$this.$confirm('确定这样修改').then(() => {
					let para=this.editData.form;
					editBm(para).then(data => {
						$this.diaLoading=false;
						if(!data.success){
							$this.$message.error(data.msg);
							return false;
						}
						$this.$message({
						message:'修改成功',
						type:'success'
					});
					$this.editData.dialogVisible=false;
					$this.getBms();
					});
					return false;
				});
			},
			EditEach(index,row){
				this.editData.type=1;
				this.editData.form={
					name:row.name,
					bz:row.bz,
					flag:!!row.stop_flag,
					tysm:''
				};
				this.editData.dialogVisible=true;
			},
			typeChange(val){
				this.filter.type=val;
				this.filter.status='所有';
				this.getBms();
			}
		},
		mounted(){
			this.getBms();
		}
	}
</script>

<style>
</style>


