<template>
	<el-dialog  :data="showData" :title="calcTitle()" :visible.sync="showData.dialogVisible">
      <el-form ref="diaform" :model="showData.form" label-width="80px" :rules='showData.rules'>
        <el-form-item :label="calcLabel()" prop='name'>
          <el-input v-model="showData.form.name"></el-input>
        </el-form-item>
        <el-form-item label="备注">
          <el-input v-model="showData.form.bz" type="textarea" :resize="showData.resize" :rows='showData.rows||4'></el-input>
        </el-form-item>
        <el-form-item label='是否停用' v-if='showData.type!=0'>
			<el-checkbox v-model="showData.form.flag"></el-checkbox>
		</el-form-item>
		<el-form-item label="停用说明" prop='tysm' v-if='showData.type!=0'>
          <el-input v-model="showData.form.tysm" type="textarea" :resize="showData.resize" :rows='showData.rows||4'></el-input>
        </el-form-item>
      </el-form>
      <span slot="footer" class="dialog-footer">
        <el-button @click="cancel">取 消</el-button>
        <el-button type="primary" @click="sure">确 定</el-button>
      </span>
    </el-dialog>
</template>

<script>
	export default{
		name:'newbmdialog',
		props:{
			showData:{
				type:Object,
				default(){
					return{
						type:0,
						dialogVisible:false,
						resize:'none',
						notClearForm:false, 
						rows:4,
						form:{
							name:'',
							bz:'',
							tysm:'',
							flag:0
						},
						rules:{
							name:[
								{ required: true, message: '编码名称不能为空', trigger: 'blur' }
							]
						}
					}
				}
			}
		},
		methods:{
			calcTitle(){
				let $this=this;
				if($this.showData.title)return $this.showData.title;
				else return $this.showData.type==0?'新增编码':'编辑编码'
			},
			calcLabel(){
				let $this=this;
				if($this.showData.label)return $this.showData.label;
				else return '编码名称';
			},
			sure(){
				var $this=this;
				var back=function(){
					$this.$refs.diaform.resetFields();
				};
				this.$refs.diaform.validate((valid) => {
				this.$emit("OnSure",valid,back);
				});
			},
			cancel(){
				this.showData.dialogVisible=false;
				this.$refs.diaform.resetFields();
				if(!this.showData.notClearForm){
					this.form={
						name:'',
						bz:'',
						tysm:'',
						flag:0
					}
				}
			}
		}
	}
</script>

<style>
</style>


